import { makeStyles } from '@material-ui/core/styles'
import { Card, CardContent, CardActions, IconButton, Typography, Collapse, Paper } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({  
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
}))



export default function ElasticIp({data}) {
  const classes = useStyles()
  
  if (!data) {
    return (<p></p>)
  }
  let components = []


  if (data.error) {
    components.push(<Typography>‚ùå {data.error.message}</Typography>)
  }
  
  components.push(<Typography key="1">{!data.InstanceProfileArn ? "‚ùå" : "‚úÖ"}An IAM role has been attached to this instance {data.InstanceProfileArn}</Typography>)


  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
        IAM
        </Typography>
        {components}
      </CardContent>
    </Card>
  )
}