import { makeStyles } from '@material-ui/core/styles'
import { Card, CardContent, CardActions, IconButton, Typography, Collapse, Paper } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({  
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
}))



export default function S3Bucket({data, bucketName}) {
  const classes = useStyles()
  
  if (!data) {
    return (<p></p>)
  }
  let components = []


  if (data.error) {
    components.push(<Typography>‚ùå {data.error.message}</Typography>)
  }

  console.log("S3", data)

  const { date } = data
  
  components.push(<Typography key="bucket-1">{(!date) ? "‚ùå" : "‚úÖ"} File uploaded and downloaded to {bucketName} at {date}</Typography>)

  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
        S3
        </Typography>
        {components}
      </CardContent>
    </Card>
  )
}