import './App.css'
import { useEffect, useState } from 'react'
import * as ec2Helper from './ec2Helper'
import axios from 'axios'

import useLocalStorage from './useLocalStorage'

import SecurityGroups from './components/SecurityGroups'
import Service from './components/Service'
import S3Bucket from './components/S3Bucket'
import IAMExists from './components/IAMExists'

import IPSubmit from './components/IPSubmit'

import { CircularProgress, Button } from '@material-ui/core'

function App() {

  const [publicIp, setPublicIp] = useLocalStorage('publicIp', "");
  const [details, setDetails] = useState({})
  const [pageState, setPageState] = useState("unsearched")

  const serviceName = "nodeserver.service"
  const subnetCIDRs = ["10.0.1.0/24", "10.0.2.0/24", "10.0.3.0/24", "10.0.4.0/24"]
  const instanceIps = ["10.0.1.10", "10.0.2.10", "10.0.3.10"]
  const vpcCIDR = "10.0.0.0/16"

  const _details = {
    service: "/api/status/service",
    securityGroups: "/api/status/securityGroups",
    iaminfo: "/api/status/iaminfo",
    s3: "/api/status/s3",
    general: "/api/status"
  }
  
  
  const submit = async () => {
    setPageState("loading")
    const details = await ec2Helper.getDetails(_details)
    console.log(details)
    setDetails(details)
    setPageState("loaded")
  }

  const submitToken = async () => {
    setPageState("loading")
    try {
      const data = await axios.post(`http://52.60.55.210`, {
        host: window.location.host
      })
      setPageState("loaded")
      if (data.error) {
        alert("Error")
        return
      }
    } catch (error) {
      setPageState("loaded")
      console.log(error)
      alert("Error")
      return
    }
    
    alert("ü§ó")
  }

  const sgs = [
    {fromPort: 4040, toPort: 4040, cidr: "0.0.0.0/0"},
    {fromPort: 22, toPort: 22, cidr: "0.0.0.0/0"}
  ]

  return (
    <div className="App">

      {/* {pageState !== "loading" && <IPSubmit ipAddress={publicIp} onSubmit={ipSubmit}></IPSubmit> } */}
      {pageState !== "loading" && <Button variant="contained" color="primary" onClick={submit}>Submit</Button>
 }

      {pageState === "loading" && <><h1>Loading...</h1> <CircularProgress /></>}

      {pageState === "loaded" && 
      <>
      <h2>Main Server:</h2>
      {/* <Details data={details} privateIp={privateIp}></Details>
      <ElasticIp data={elasticIp}></ElasticIp>
      <RouteTable routeTable={routeTable}></RouteTable> */}
      <IAMExists data={details.iaminfo}></IAMExists>
      <SecurityGroups data={details.securityGroups} expectedGroups={sgs}></SecurityGroups>
      <Service data={details.service} serviceName={serviceName}></Service>
      <S3Bucket data={details.s3} bucketName={details.general.env.bucketName}></S3Bucket>
 

      <br></br>

      <p>This assignment is pass or fail. You need all green checkmarks to pass.</p>
      <p>Only click this button if all the checkmarks are green.</p>
      <Button variant="contained" color="primary" onClick={() => submitToken()}>Submit Token</Button>
      </>
      }

    </div>
  )
}

export default App
