import { makeStyles } from '@material-ui/core/styles'
import { Card, CardContent, CardActions, IconButton, Typography, Collapse, Paper } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({  
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
}))



export default function Service({data, serviceName}) {
  const classes = useStyles()
  
  if (!data) {
    return (<p></p>)
  }
  let components = []


  if (data.error) {
    components.push(<Typography>‚ùå {data.error.message}</Typography>)
  }

  console.log(data)

  const { ActiveState, SubState } = data
  
  components.push(<Typography key="service-1">{(ActiveState !== "active") ? "‚ùå" : "‚úÖ"} {serviceName} is a service in an an active state: <em>{ActiveState}</em></Typography>)
  components.push(<Typography key="service-2">{(SubState !== "running") ? "‚ùå" : "‚úÖ"} {serviceName} is a service in an a running substate: <em>{SubState}</em></Typography>)


  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
        Service
        </Typography>
        {components}
      </CardContent>
    </Card>
  )
}