import axios from 'axios'

export default function network(ip, forwardIp) {

  function errorObject(error) {
    const status = (error.response && error.response.status) || error.code
  
    const messages = {
      502: "Can reach nginx, but not the server. It's probably not currently running on port 8080",
      "ECONNABORTED": "Couldn't connect to the instance's nginx server"
    }
  
    return {
      status,
      message: messages[status]
    }
  }

  async function makeLocalRequest(url, method="get") {
    const path = `${url}` + (!!forwardIp ? `?forward=${forwardIp}` : "")
    console.log(path)
    try {
      const data = await axios[method](path, {timeout: 10000})
      return data.data
    } catch (error) {
      // debugger
      return {error: {...error, ...errorObject(error)}}
    }
  }
  
  async function makeRequest(url, method="get") {
    const path = `http://${ip}${url}` + (!!forwardIp ? `?forward=${forwardIp}` : "")
    console.log(path)
    try {
      const data = await axios[method](path, {timeout: 10000})
      return data.data
    } catch (error) {
      // debugger
      return {error: {...error, ...errorObject(error)}}
    }
  }

  return { makeRequest, makeLocalRequest }

}  
