import React, { useState } from 'react'

import { makeStyles } from '@material-ui/core/styles'
import clsx from 'clsx'
import { Card, CardContent, CardActions, IconButton, Typography, Collapse, Paper } from '@material-ui/core'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

const useStyles = makeStyles((theme) => ({  
  root: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  expand: {
    transform: 'rotate(0deg)',
    // marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  sgPaper: {
    padding: 10
  }
}))

function securityGroups(groups, classes) {
  return (
    <Paper elevation={3}>
    {groups.map((permissions, gi) => (
      
      permissions.map((p, pi) => (
        <Card variant="outlined" key={gi+""+pi} className={classes.sgPaper}>
        <Typography>Group {gi}, Permission {pi}:</Typography>
        <ul>
        <li>From Port {p.fromPort}</li>
        <li>To Port {p.fromPort}</li>
        <li>Cidr {p.cidr}</li>
        </ul>
        </Card>
      ))
      
    ))}
    </Paper>
  )
}

function parsePermission(permission) {
  return {
    fromPort: permission.FromPort,
    toPort: permission.ToPort,
    cidr: permission.IpRanges[0].CidrIp
  }
}

function parseSecurityGroup(group) {
  const permissions = group.IpPermissions.map(parsePermission)
  return permissions
}


export default function SecurityGroups({data, expectedGroups}) {
  const classes = useStyles()
  const [expanded, setExpanded] = useState(false)

  const handleExpandClick = () => {
    setExpanded(!expanded)
  }

  if (!data) {
    return (<p></p>)
  }
  let components = []


  if (data.error) {
    components.push(<Typography>‚ùå {data.error.message}</Typography>)
  }
  
  const groups = data.SecurityGroups && data.SecurityGroups.map(parseSecurityGroup)
  const flattenedPermissions = groups ? groups.flat() : []
  components = expectedGroups.map((expected, idx) => (
    <Typography key={`group${idx}`}>{!
      (flattenedPermissions.find(group => 
        group.fromPort === expected.fromPort &&
        group.toPort === expected.toPort &&
        group.cidr === expected.cidr
      )) ? "‚ùå" : "‚úÖ"} Security group from {expected.cidr} on ports {expected.fromPort} to {expected.toPort}</Typography>
  ))
   

  // components.push(<Typography key="1">{!port80 ? "‚ùå" : "‚úÖ"} Add a security group with access to port 80</Typography>)


  return (
    <Card className={classes.root} variant="outlined">
      <CardContent>
        <Typography className={classes.title} color="textSecondary" gutterBottom>
        Security Groups
        </Typography>
        {components}
      </CardContent>
      <CardActions disableSpacing>
        <Typography>Show Groups</Typography>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
          <CardContent>
            <Typography paragraph>Security Groups:</Typography>
            {securityGroups(groups || [], classes)}
          </CardContent>
      </Collapse>
    </Card>
  )
}