import network from './network'

export default function ec2(ip, forwardIp) {
  const { makeRequest, makeLocalRequest } = network(ip, forwardIp)

  async function get(url) {
    const data = await makeLocalRequest(url)
    return data
  }
  
  async function getElasticIp() {
    const data = await makeRequest(`/addresses`)
    return data
  }
  
  async function getDetails() {
    const data = await makeRequest(`/details`)
    return data
  }
  
  async function getSecurityGroups() {
    const data = await makeRequest(`/securityGroups`)
    return data
  }
  
  async function getThisRouteTable() {
    const data = await makeRequest(`/thisRouteTable`)
    return data
  }

  async function getVpc() {
    const data = await makeRequest(`/vpcs`)
    return data
  }

  async function getSubnets() {
    const data = await makeRequest(`/subnets`)
    return data
  }

  async function getService(service) {
    const data = await makeRequest(`/service/${service}`)
    return data
  }

  async function getEndpoints() {
    const data = await makeRequest(`/endpoints`)
    return data
  }

  async function getVolumes() {
    const data = await makeRequest(`/volumes`)
    return data
  }

  async function testBucket() {
    const data = await makeRequest(`/testBucket`)
    return data
  }

  return {
    get,
    getElasticIp,
    getDetails,
    getSecurityGroups,
    getThisRouteTable,
    getVpc,
    getSubnets,
    getService,
    getEndpoints,
    testBucket,
    getVolumes
  }
}
